#contact h3 {
    margin-bottom: 2rem;
}

.contact__container {
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.contact__options {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.contact__option {
    background: var( --colour-bg-variant );
    padding: 1rem;
    border-radius: .6rem;
    border: 2px solid var( --colour-primary-variant );
    text-align: center;
    transition: var( --transition );
}

.contact__option:hover {
    border-color: aliceblue;
}

.contact__option-icon {
    font-size: 1.5rem;
}

/***** FORM ****/

form {
    min-width: 350px;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    align-items: center;

}

input,
textarea {
    width: 100%;
    padding: 1.5rem;
    font-size: large;
    background: var( --colour-bg-variant );
    color: var( --text-light );
    border-radius: .4rem;
    border:  2px solid var( --colour-primary-variant );
    resize: none;
}

input:focus,
textarea:focus {
    border-color: var( --colour-white );
}

input::placeholder,
textarea::placeholder {
    color: var(--colour-primary);
    font-size: large;
}

form button {
    margin-left: auto;
    margin-right: auto;
    font-size: large;
}

form button .send__button-icon {
    margin-top: .2rem;
    font-size: 1rem;
}


/* ===== Medium Devices ==== */
@media screen and ( max-width: 992px ) {
    .contact__container {
        flex-direction: column;
    }

    .contact__options {
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-bottom: 2rem;
    }

    input,
    textarea {
        max-width: 700px;
    }
    
}

/* ==== Small Devices === */
@media screen and ( max-width: 576px ) {
    .contact__options {
        flex-direction: column;
        flex-wrap: wrap;
    }

    .contact__options .contact__option,
    form {
        width: 90vw;
        margin-left: auto;
        margin-right: auto;
    }

    button {
        margin-top: 2rem;
    }

}